import Home from './components/Home'
import Menu from './components/Menu'
import Admin from './components/Admin'
import About from './components/about/About'
import Login from './components/Login'
import Register from './components/Register'

//二级路由
import Contact from './components/about/Contact'
import Delivery from './components/about/Delivery'
import History from './components/about/History'
import OrderingGuide from './components/about/OrderingGuide'

//三级路由
import Person from './components/about/contact/person'
import PersonName from './components/about/contact/personName'



export const routes = [
    {path:"/",components:{
        default:Home,
        "orderingGuide":OrderingGuide,
        "delivery":Delivery,
        "history":History
    }},
    {path:"/menu",component:Menu},
    {path:"/admin",component:Admin,
    // beforeEnter:(to,from,next)=>{
    //   alert("非登录状态，不能访问此页面");
    //   // next();
    //   next(false);
    // }
  },
    {path:"/about",component:About,children:[
      {path:"/about/contact",name:"contactLink",component:Contact,children:[
        {path:"/contact/person",name:"personLink",component:Person},
        {path:"/personName",name:"personNameLink",component:PersonName}
      ]},
      {path:"/history",name:"historyLink",component:History},
      {path:"/delivery",name:"deliveryLink",component:Delivery},
      {path:"/orderingGuide",name:"orderingGuide",component:OrderingGuide}
    ]},
    {path:"/login",component:Login},
    {path:"/register",component:Register},
    {path:"*",redirect:"/"}
  ]


